.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@keyframes animateUp{
  from{ bottom: 0px; }
  to{ bottom: 98px; }
}
@keyframes animateDown{
  from{ top: 0px; }
  to{ top: 98px; }
}
